import React, { Component } from 'react'
import classnames from 'classnames';
import { Tween } from 'react-gsap';
import './OurApproachSliderSection.scss';
import LandingPageHeader from './LandingPageHeader.jsx';
import SocialIcons from './SocialIcons';
import Container from '../../../components/Container';
import WaveBackground from '../../../components/WaveBackground';
import HorizontalSlider from '../../../components/HorizontalSlider';

export default class OurApproachSliderSection extends Component {
   constructor() {
      super();
      this.state = {
         activeSlide: 0,
         imgPosition: 0,
      }

      this.slides = [
         {
            title: '01 Lean aanpak',
            imgSrc: '/images/our-appoach/our-appoach-01.png',
            content: [
               'Alle zaken in het web ontwikkelings process dat geen waarde toevoegt voor onze klanten wordt beschouwd als "waste" en hoort daarom niet thuis in het ontwikkelingsproces. Door onze lean aanpak zorgen we ervoor dat alleen de meest belangrijke kenmerken snel en als eerste geleverd worden.',
               'Wanneer we samenwerken tijdens het ontwikkelingsproces zullen we ervoor zorgen dat de laagste prioriteit-functies worden vervangen door belangrijkere ideeën wat resulteert in een lean en betekenisvolle applicatie,  precies zoals onze klanten willen.',
            ],
         },
         {
            title: '02 Agile Management',
            imgSrc: '/images/our-appoach/our-appoach-02.png',
            content: [
               'Om te kunnen concurreren in de snel bewegende markt van vandaag moeten organisaties innovatie als een onderdeel van hun business zien. Door een agile workflow kunt u sneller resultaten boeken.',
               'Adaptiviteit en flexibiliteit staan hoog in het vaandel door het gebruik van agile web ontwikkelings services. Hierdoor blijft de focus op de ontwikkeling en niet op bijvoorbeeld een overweldigende hoeveelheid documentatie. Keenethics levert een agile ervaring die er voor zorgt dat u er op kunt vertrouwen dat u snel resultaten boekt.',
            ],
         },
         {
            title: '03 Kwaliteit',
            imgSrc: '/images/our-appoach/our-appoach-03.png',
            content: [
               'Wij zijn er trots op dat wij custom web development services aanbieden. Van strategie tot ontwikkeling en alles ertussen, wij integreren onszelf in uw team en gebruiken onze vergevorderde ontwikkelingsportfolio om suggesties, feedback en inzicht te geven in elke stap die gezet wordt.',
               'Of het nu gaat over UI, UX of de ontwikkeling van kerntaken, wij volgen de voorbeelden uit de praktijk om uw product naar de markt te brengen.',
            ],
         },
      ];

      this.nextSlide = this.nextSlide.bind(this);
   }

   nextSlide() {
      const { activeSlide, imgPosition } = this.state;

      const nextSlide = activeSlide === this.slides.length - 1 ? 0 : activeSlide + 1;
      this.setState({ activeSlide: nextSlide, imgPosition: imgPosition + 1 })
   }

   render() {
      const { activeSlide, imgPosition } = this.state;
      const { isActive, style, changeSlide, isMobileView, toggleHamburgerMenu, openContactsModal } = this.props;

      const playState = `${isActive ? 'play' : 'reverse'}`;
      
      return (
         <section className={classnames('our-appoach-slider-section one-screen-slide', { 'one-screen-slide--active': isActive })} style={style}>
            {!isMobileView && <LandingPageHeader
               color="dark"
               changeSlide={changeSlide}
               isMobileView={isMobileView}
               toggleHamburgerMenu={toggleHamburgerMenu}
               activeItem="Onze Aanpak"
               openContactsModal={openContactsModal}
            />}
            {!isMobileView && <SocialIcons color="dark" isActive={isActive} />}
            <div className="landing-page-content-wrap">
               <Container>
                  <Tween from={{ y: -100, opacity: 0 }} duration={1.5} playState={playState}>
                     <div
                        className={classnames('our-appoach-slider--circle-wrap', {
                           '-first': activeSlide === 0,
                           '-second': activeSlide === 1,
                           '-third': activeSlide === 2,
                        })}
                        onClick={this.nextSlide}
                     >
                        <img className="our-appoach-slider--background" src={this.slides[activeSlide].imgSrc} alt={this.slides[activeSlide].title} />
                        <img className="our-appoach-slider--circle" src="/images/our-appoach/circle.png" alt="circle" style={{ transform: `rotate(${120 * imgPosition - 120}deg)` }} />
                     </div>
                  </Tween>
                  <HorizontalSlider
                     slidesToShow={1}
                     activeSlide={activeSlide}
                     isActive={isActive}
                  >
                     {
                        this.slides.map(({ title, content }) => (
                           <li key={title} className="our-appoach--slider-item">
                              <h3 className="our-appoach--item-title">{title}</h3>
                              {
                                 content.map(paragraph => (
                                    <p className="our-appoach--item-description" key={paragraph}>{paragraph}</p>
                                 ))
                              }
                           </li>
                        ))
                     }
                  </HorizontalSlider>
               </Container>
            </div>
            <WaveBackground color="red" />
         </section>
      )
  }
}
