import React, { Component } from 'react'
import PropTypes from 'prop-types';
import { Tween } from 'react-gsap';
import './Button.scss';

export default class Button extends Component {
   constructor() {
      super();
      this.state = {
         activeEffectIsVisible: false,
      }
      this.handleOnClick = this.handleOnClick.bind(this);
   }

   handleOnClick(e) {
      const { onClick } = this.props;
      if (onClick) onClick();
      this.setState({ activeEffectIsVisible: false });
      const rect = e.target.getBoundingClientRect();
      const x = Math.round(e.clientX - rect.left);
      const y = Math.round(e.clientY - rect.top);
      this.setState({ activeEffectIsVisible: true, activeEffectPositionX: x, activeEffectPositionY: y });
      setTimeout(() => this.setState({ activeEffectIsVisible: false }), 500);
   }
   render() {
      const { activeEffectIsVisible, activeEffectPositionX, activeEffectPositionY } = this.state;
      const { children, className, isActive } = this.props;

      return (
         <Tween from={{ scale: .5, opacity: 0, delay: 1 }} duration={1} playState={`${isActive ? 'play' : 'reverse'}`}>
            <button className={`button ${className}`} onClick={this.handleOnClick}>
               {children}
               <span className="button--blur"></span>
               {activeEffectIsVisible && <span className="button--active-effect" style={{ left: activeEffectPositionX, top: activeEffectPositionY }} />}
            </button>
         </Tween>
      )
   }
}
Button.propTypes = {
   children: PropTypes.node.isRequired,
   className: PropTypes.string,
   onClick: PropTypes.func,
};
Button.defaultProps = {
   className: '',
}
