      //       'Alle zaken in het web ontwikkelings process dat geen waarde toevoegt voor onze klanten wordt beschouwd als "waste" en hoort daarom niet thuis in het ontwikkelingsproces. Door onze lean aanpak zorgen we ervoor dat alleen de meest belangrijke kenmerken snel en als eerste geleverd worden.',
