import React, { Component } from 'react'
import classnames from 'classnames';
import { Tween } from 'react-gsap';
import './ContactsModal.scss';
import { withStyles } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import TextField from '@material-ui/core/TextField';
import IconButton from '@material-ui/core/IconButton';

export default class ContactsModal extends Component {
   constructor() {
      super();
      this.state = {
         name: '',
         lastName: '',
         email: '',
         phoneNumber: '',
         message: '',
         error: {},
         isSending: false
      }
      this.handleFormSubmit = this.handleFormSubmit.bind(this);
   }
   handleFormSubmit(e){
      e.preventDefault()
      const { name, email, phoneNumber, message } = this.state;

      fetch('/contacts', {
         method: 'POST',
         headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
         },
         body: JSON.stringify({
            name,
            email,
            phoneNumber,
            message,
         })
      })
      .then((response) => response.json())
      .then((responseJson) => {
         if (responseJson.success) {
            this.setState({ isSending: true })
         }
         else this.setState({ isSending: false })
      })
      .catch((error) => {
         console.error(error);
      });
   }

   render() {
      const { name, email, phoneNumber, message } = this.state;
      const { isOpen, onClose } = this.props;

      return (
         <Dialog
            onClose={onClose}
            aria-labelledby="contacts-modal"
            open={isOpen}
            className="contacts-modal"
         >
            <IconButton aria-label="Close" onClick={onClose} className="contacts-modal--close" />
            <MuiDialogTitle className="contacts-modal--title">
               Neem contact met ons op
            </MuiDialogTitle>
            <MuiDialogContent className="contacts-modal--body">
               <form
                  autoComplete="off"
                  onSubmit={this.handleFormSubmit}
               >
                  <TextField
                     className="contacts-modal--input"
                     label="Je naam"
                     type="text"
                     fullWidth
                     onChange={e => this.setState({ name: e.target.value })}
                     value={name}
                     required
                     variant="outlined"
                  />
                  <TextField
                     className="contacts-modal--input"
                     label="Je e-mailadres"
                     type="email"
                     fullWidth
                     onChange={e => this.setState({ email: e.target.value })}
                     value={email}
                     required
                     variant="outlined"
                  />
                  <TextField
                     className="contacts-modal--input"
                     label="Je telefoon nummer"
                     type="phone"
                     fullWidth
                     onChange={e => this.setState({ phoneNumber: e.target.value })}
                     value={phoneNumber}
                     variant="outlined"
                  />
                  <TextField
                     className="contacts-modal--input"
                     label="Vertel ons over uw idee"
                     fullWidth
                     multiline
                     rows="3"
                     rowsMax="9"
                     onChange={e => this.setState({ message: e.target.value })}
                     value={message}
                     variant="outlined"
                  />
                  <button type="submit" className="contacts-modal--submit">
                     Send
                     <svg height="26" xmlns="http://www.w3.org/2000/svg" viewBox="-3305 9588 25.73 24.957"><defs></defs><g data-name="Send" transform="translate(-3305 9587.613)"><path d="M24.408.6L.706 13.026c-.975.511-.935 1.26.088 1.665l3.026 1.2a3.88 3.88 0 0 0 3.437-.494L20.4 5.03c.864-.681.957-.58.206.224L10.215 16.377a1.258 1.258 0 0 0 .495 2.182l.354.139c1.024.4 2.7 1.062 3.717 1.478l3.356 1.366c1.02.415 1.854.759 1.854.765s.006.025.011.026.246-.864.534-1.926L25.654 1.6c.288-1.063-.271-1.513-1.246-1z"/><path d="M10.324 19.82L8 18.87c-1.018-.417-1.506.072-1.084 1.089 0 0 2.156 5.194 2.1 5.377s2.068-3.082 2.068-3.082a1.6 1.6 0 0 0-.76-2.434z"/></g></svg>
                  </button>
               </form>
            </MuiDialogContent>
         </Dialog>
      )
   }
}
