import React from 'react';
import classnames from 'classnames';
import { Tween } from 'react-gsap';
import './HomeSection.scss';
import LandingPageHeader from './LandingPageHeader.jsx';
import SocialIcons from './SocialIcons';
import OurContacts from './OurContacts';
import Container from '../../../components/Container';
import Orbits from '../../../components/Orbits';
import Button from '../../../components/Button';
import WaveBackground from '../../../components/WaveBackground';

export default function HomeSection({ isActive, style, changeSlide, isMobileView, toggleHamburgerMenu, openContactsModal }) {
   const playState = `${isActive ? 'play' : 'reverse'}`;
   return (
      <section className={classnames('home-section one-screen-slide', { 'one-screen-slide--active': isActive })} style={style}>
         {!isMobileView && <LandingPageHeader
            color="dark"
            changeSlide={changeSlide}
            isMobileView={isMobileView}
            toggleHamburgerMenu={toggleHamburgerMenu}
            activeItem="Neen contact"
            openContactsModal={openContactsModal}
         />}
         {!isMobileView && <SocialIcons color="dark" isActive={isActive} />}
         {!isMobileView && <OurContacts color="dark" isActive={isActive} />}
         <Orbits colorRgb="222, 79, 79" />
         <div className="landing-page-content-wrap">
            <Container>
               <Tween from={{ y: -200, opacity: 0 }} duration={1.5} playState={playState}>
                  <h2 className="landing-page--title -home-section">Laat uw bedrijf vandaag nog groeien</h2>
               </Tween>
               <Button
                  isActive={isActive}
                  onClick={openContactsModal}
               >Neem contact op</Button>
            </Container>
         </div>
         <WaveBackground color="red" />
      </section>
   )
}
