import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import './Container.scss';

export default function Container({ children, className, isColumnsWrap, isFullWidth }) {
    return (
        <div className={classnames(`container ${className}`, {'-column-wrap': isColumnsWrap, '-full-width': isFullWidth})}>
            {children}
        </div>
    )
}
Container.propTypes = {
    children: PropTypes.node.isRequired,
    className: PropTypes.string,
    isColumnsWrap: PropTypes.bool,
    isFullWidth: PropTypes.bool,
};
Container.defaultProps = {
    className: '',
    isColumnsWrap: false,
    isFullWidth: false,
}