import React from 'react';
import PropTypes from 'prop-types';
import { Tween, Timeline } from 'react-gsap';
import { Link, Element , Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'
import './HamburgerMenu.scss';
import LandingPageHeader from './LandingPageHeader';
import Orbits from '../../../components/Orbits';
import SocialIcons from './SocialIcons';
import OurContacts from './OurContacts';

export default function HamburgerMenu({ isOpen, onClose, openContactsModal }) {
	if (!isOpen) return null;

	const navigation = [
		{
			title: 'Wie zijn wij',
			linkTo: 'aboutUsSection',
		},
		{
			title: 'Onze Aanpak',
			linkTo: 'ourApproachSection',
		},
		{
			title: 'Ervaringen',
			linkTo: 'ourExperiencesSection',
		},
		{
			title: 'Neen contact',
			onClick: openContactsModal,
		},
	];

	return (
		<Tween from={{ opacity: 0 }} duration={.3}>
			<div className="hamburger-menu">
				<LandingPageHeader
					color="light"
					isHamburger
					toggleHamburgerMenu={onClose}
					isMobileView
					logoOnClick={onClose}
				/>
				<Orbits scale={2} />
				<Tween from={{ y: -50, opacity: 0, delay: .2 }} duration={.4} >
					<ul className="hamburger-menu--list">
						{
							navigation.map(({ title, linkTo, onClick }) => {
								const button = <button className="hamburger-menu--button" onClick={() => onClick ? onClick() : onClose()}>{title}</button>;
								return (
										<li className="hamburger-menu--item" key={title}>
											{
												linkTo ? <Link to={linkTo} smooth={true} offset={-50}>
													{button}
												</Link> : button
											}
										</li>
								);
							})
						}
					</ul>
				</Tween>
				<Tween from={{ x: -100, opacity: 0, delay: .4 }} duration={.4}><div><OurContacts color="light" isHamburger /></div></Tween>
				<Tween from={{ y: 100, opacity: 0, delay: .6 }} duration={.4}><div><SocialIcons color="light" isHamburger /></div></Tween>
			</div>
		</Tween>
	)
}
HamburgerMenu.propTypes = {
	onClose: PropTypes.func.isRequired,
	changeSlide: PropTypes.func.isRequired,
	isOpen: PropTypes.bool,
};
HamburgerMenu.defaultProps = {
	isOpen: false,
}