import React, { Component } from 'react';
import classnames from 'classnames';
import { Tween } from 'react-gsap';
import './OurApproachPortfolio.scss';
import LandingPageHeader from './LandingPageHeader.jsx';
import SocialIcons from './SocialIcons';
import Container from '../../../components/Container';
import Orbits from '../../../components/Orbits';
import Button from '../../../components/Button';
import WaveBackground from '../../../components/WaveBackground';
import HorizontalSlider from '../../../components/HorizontalSlider';

export default class OurApproachPortfolio extends Component {
   constructor(props) {
      super(props);

      this.state = {
         slidesToShow: window.innerWidth < 1100 ? 2 : 3,
      }
      this.handleResize = this.handleResize.bind(this);
   }

   componentDidMount() {
      window.addEventListener('resize', this.handleResize);
   }

   componentWillUnmount() {
      window.removeEventListener('resize', this.handleResize);
   }

   handleResize() {
      this.setState({ slidesToShow: window.innerWidth < 1100 ? 2 : 3 });
   }


   render() {
      const { slidesToShow } = this.state;
      const { isActive, style, changeSlide, isMobileView, toggleHamburgerMenu, openContactsModal } = this.props;

      const portfolioList = [
         {
            title: 'My Grass Growth',
            description: 'Keep you cows healthy and well-fed',
            technology: ['Meteor', 'Blaze'],
            imgSrc: '/images/landing-page/portfolio/my-grass-growth',
            href: '//keenethics.com/project-my-grass-growth',
         },
         {
            title: 'SelfLeaders',
            description: 'Personal Growth Tool',
            technology: ['Meteor', 'GraphQL', 'React', 'Blaze'],
            imgSrc: '/images/landing-page/portfolio/self-leaders',
            href: '//keenethics.com/project-self-leaders',
         },
         {
            title: 'Pree',
            description: 'Stop losing your stuff',
            technology: ['Meteor', 'React', 'React Native', 'Mongo'],
            imgSrc: '/images/landing-page/portfolio/pree',
            href: '//keenethics.com/project-pree',
         },
      ];
   
      const content = [];
      portfolioList.forEach(({ title, href, imgSrc, description, technology }, key) => {
         if (isMobileView && key > 2) return null;
         content.push(
            <li
               className="our-appoach-portfolio--item"
               key={title}
               style={!isMobileView ? { width: `${100 / slidesToShow}%` } : null}
            >
               <a href={href} target="_blank" rel="noopener noreferrer" className="our-appoach-portfolio--link">
                  <figure className="our-appoach-portfolio--figure">
                     <picture>
                        <source srcSet={`${imgSrc}.webp`} type="image/webp" />
                        <source srcSet={`${imgSrc}.jpg`} type="image/jpeg" /> 
                        <img src={`${imgSrc}.jpg`} alt={title} className="our-appoach-portfolio--image" />
                     </picture>
                     <figcaption className="our-appoach-portfolio--figcaption">
                        <h3 className="our-appoach-portfolio--title">{title}</h3>
                        <p className="our-appoach-portfolio--description">{description}</p>
                        {
                           !isMobileView && <ul className="our-appoach-portfolio--technology-list">
                              {
                                 technology.map(t => <li className="our-appoach-portfolio--technology-item" key={t}>{t}</li>)
                              }
                           </ul>
                        }
                     </figcaption>
                  </figure>
               </a>
            </li>
         )
      })
      !isMobileView && content.push(
         <li
            key="visit-portfolio"
            className="our-appoach-portfolio--item -more"
            style={!isMobileView ? { width: `${100 / slidesToShow}%` } : null}
         >
            Bezoek
            <a
               href="//keenethics.com/portfolio"
               target="_blank"
               rel="noopener noreferrer"
            >
               onze portfolio...
            </a>
         </li>
      )

      const playState = `${isActive ? 'play' : 'reverse'}`;

      return (
         <section className={classnames('our-appoach-portfolio-section one-screen-slide', { 'one-screen-slide--active': isActive })}  style={style}>
            {!isMobileView && <LandingPageHeader
               color="dark"
               changeSlide={changeSlide}
               isMobileView={isMobileView}
               toggleHamburgerMenu={toggleHamburgerMenu}
               activeItem="Onze Aanpak"
               openContactsModal={openContactsModal}
            />}
            {!isMobileView && <SocialIcons color="dark" isActive={isActive} />}
            <Orbits />
            <div className="landing-page-content-wrap">
               <Container>
                  <Tween from={{ y: -200, opacity: 0 }} duration={1.5} playState={playState}>
                     <h2 className="landing-page--title">Onze Portfolio</h2>
                  </Tween>
                  <Tween from={{ y: 100, opacity: 0 }} duration={1.5} playState={playState}>
                     <div className="our-appoach-portfolio">
                        {
                           !isMobileView ? (<HorizontalSlider
                              slidesToShow={slidesToShow}
                              buttonsType="arrows"
                              isActive={isActive}
                           >
                              {content}
                           </HorizontalSlider>
                           ) : <ul className="our-appoach-portfolio--list">{content}</ul>
                        }
                     </div>
                  </Tween>
                  {isMobileView && <a href="//keenethics.com/portfolio" className="our-appoach-portfolio--button-wrap" target="_blank" rel="noopener noreferrer"><Button isActive={isActive}>Bezoek onze portfolio</Button></a>}
               </Container>
            </div>
            <WaveBackground color="red" />
         </section>
      )
   }
}