import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Swipe from 'react-easy-swipe';

export default class OneScreenSlider extends Component {
   constructor(props) {
      super(props);
      this.state = {
         swiped: 0,
         disableScroll: '',
      }
      this.onScroll = this.onScroll.bind(this);
      this.onSwipeMove = this.onSwipeMove.bind(this);
      this.onSwipeEnd = this.onSwipeEnd.bind(this);
      this.onSwipe = this.onSwipe.bind(this);
   }

   componentDidMount() {
      window.addEventListener('mousewheel', this.onScroll, false);
      window.addEventListener("DOMMouseScroll", this.onScroll, false);
	}

	componentWillUnmount() {
      window.removeEventListener('mousewheel', this.onScroll, false);
      window.removeEventListener("DOMMouseScroll", this.onScroll, false);
   }
   
   onScroll(e) {
      const { children, activeSlide, changeSlide, hamburgerMenuIsOpen, isMobileView } = this.props;
      if (isMobileView || hamburgerMenuIsOpen) return;
      if (e.wheelDelta && Math.abs(e.wheelDelta) < 40) return;

      const delta = Math.max(-1, Math.min(1, e.wheelDelta || -e.detail));
      if (delta === 0) return;
      const isScrollDown = delta < 0;
      const isScrollUp = delta > 0;

      // Check if section higher than 100vh
      const activeSlideContaiver = document.querySelector('.one-screen-slide--active .landing-page-content-wrap .container');
      if ((isScrollDown && activeSlideContaiver.getBoundingClientRect().bottom > window.innerHeight) || (isScrollUp && activeSlideContaiver.getBoundingClientRect().top < 50) ) {
         window.removeEventListener('mousewheel', this.onScroll, false);
         window.removeEventListener("DOMMouseScroll", this.onScroll, false);
         setTimeout(() => {
            window.addEventListener('mousewheel', this.onScroll, false);
            window.addEventListener("DOMMouseScroll", this.onScroll, false);
         }, 500);
         return;
      }

      const nextSlide = isScrollDown ? activeSlide + 1 : activeSlide - 1;
      if (nextSlide < 0 || nextSlide === children.length) return;
      changeSlide(nextSlide);
      
		window.removeEventListener('mousewheel', this.onScroll, false);
      window.removeEventListener("DOMMouseScroll", this.onScroll, false);
		setTimeout(() => {
         window.addEventListener('mousewheel', this.onScroll, false);
         window.addEventListener("DOMMouseScroll", this.onScroll, false);
      }, 500);
   }
   
   onSwipeEnd(e) {
      this.setState({ swiped: 0 });
   }
   onSwipeMove(position) {
      if (Math.abs(position.x) > Math.abs(position.y)) return;
      const activeSlideContaiver = document.querySelector('.one-screen-slide--active .landing-page-content-wrap .container');
      console.log(position.y)
      if ((position.y < 0 && activeSlideContaiver.getBoundingClientRect().bottom > window.innerHeight) || (position.y > 0 && activeSlideContaiver.getBoundingClientRect().top < 0) ) {
         return;
      }
      this.setState({ swiped: position.y });
   }

   onSwipe(direction) {
      const { swiped } = this.state;
      const { children, changeSlide, activeSlide } = this.props;

      if (-40 < swiped && swiped < 40) return;

      const activeSlideContaiver = document.querySelector('.one-screen-slide--active .landing-page-content-wrap .container');
      if ((direction === 'up' && activeSlideContaiver.getBoundingClientRect().bottom > window.innerHeight) || (direction === 'down' && activeSlideContaiver.getBoundingClientRect().top < 0) ) {
         return;
      }
      const nextSlide = direction === 'up' ? activeSlide + 1 :activeSlide - 1;
      if (nextSlide < 0 || nextSlide === children.length) return;
      changeSlide(nextSlide);
   }

   renderSections(child, key, style = {}) {
      const { isMobileView, changeSlide, activeSlide, toggleHamburgerMenu, openContactsModal } = this.props;

      return (
         React.cloneElement(child, {
            style,
            key,
            isActive: isMobileView || activeSlide === key,
            changeSlide: changeSlide,
            isMobileView: isMobileView,
            toggleHamburgerMenu: toggleHamburgerMenu,
            openContactsModal: openContactsModal,
            disableScroll: this.disableScroll,
         })
      )
   }

   renderSectionsForMobile() {
      const { children, activeSlide, isMobileView } = this.props;
      return (
         <div className="disable-slider">
            {
               children.map((child, key) => this.renderSections(child, key))
            }
         </div>
      )
   }

   render() {
      const { swiped } = this.state;
      const { children, activeSlide, isMobileView } = this.props;

      const isIE11 = !!window.MSInputMethodContext && !!document.documentMode;

      if (isMobileView) return this.renderSectionsForMobile();;

      return (
         <Swipe
            onSwipeEnd={this.onSwipeEnd}
            onSwipeMove={this.onSwipeMove}
            onSwipeDown={() => this.onSwipe('down')}
            onSwipeUp={() => this.onSwipe('up')}
         >
            <div className="one-screen-slider">
               {
                  children.map((child, key) => {
                     const style = {
                        zIndex: children.length - key,
                        transform: `translateY(${swiped}px)`,
                     }
                     if (activeSlide === key) {
                        style.transform = `translateY(${swiped}px)`;
                     } else if (!isIE11 && (key === activeSlide + 1 || key === activeSlide - 1)) {
                        style.transform = activeSlide < key ? `translateY(calc(100% + ${swiped}px))` : `translateY(calc(${swiped}px - 100%))`;
                     } else {
                        style.transform = activeSlide < key ? `translateY(100%)` : `translateY(-100%)`;
                     }
                     if (swiped) style.transition = 'transition .03s linear';
                     return this.renderSections(child, key, style);
                  })
               }
            </div>
         </Swipe>
      )
   }
}
OneScreenSlider.propTypes = {
   activeSlide: PropTypes.number.isRequired,
   changeSlide: PropTypes.func.isRequired,
   children: PropTypes.node.isRequired,
   isMobileView: PropTypes.bool,
   hamburgerMenuIsOpen: PropTypes.bool,
   openContactsModal: PropTypes.func,
}
OneScreenSlider.defaultProps = {
   isMobileView: false,
   hamburgerMenuIsOpen: false,
}