import React, { Component } from 'react';
import { isMobile, isTablet } from 'mobile-device-detect';
import './LandingPage.scss';
import TopSection from './components/TopSection.jsx';
import AboutUsSection from './components/AboutUsSection.jsx';
import OurApproachSection from './components/OurApproachSection.jsx';
import OurApproachPortfolio from './components/OurApproachPortfolio.jsx';
import OurApproachSliderSection from './components/OurApproachSliderSection.jsx';
import OurApproachEstimateSection from './components/OurApproachEstimateSection.jsx';
import OurApproachListSection from './components/OurApproachListSection.jsx';
import OurExperiencesSection from './components/OurExperiencesSection.jsx';
import HomeSection from './components/HomeSection.jsx';
import OneScreenSlider from '../../components/OneScreenSlider.jsx';
import HamburgerMenu from './components/HamburgerMenu.jsx';
import ContactsModal from './components/ContactsModal.jsx';
import LandingPageHeader from './components/LandingPageHeader.jsx';

export default class LandingPage extends Component {
	constructor() {
		super();
		this.state = {
			activeSlide: 0,
			hamburgerMenuIsOpen: false,
			isMobileView: window.innerWidth <= 650,
			contactsModalIsOpen: false,
		}
		this.changeSlide = this.changeSlide.bind(this);
		this.handleResize = this.handleResize.bind(this);
		this.toggleHamburgerMenu = this.toggleHamburgerMenu.bind(this);
		this.openContactsModal = this.openContactsModal.bind(this);
	}
	componentWillMount() {
      window.addEventListener('resize', this.handleResize);
   }
   componentWillUnmount() {
      window.removeEventListener('resize', this.handleResize);
	}

   handleResize() {
      if (window.innerWidth <= 650) {
         this.setState({ isMobileView: true });
      } else {
         this.setState({ isMobileView: false });
      }
	}
	toggleHamburgerMenu() {
		this.setState({ hamburgerMenuIsOpen: !this.state.hamburgerMenuIsOpen })
	}
	changeSlide(number) {
		const { contactsModalIsOpen } = this.state;
		if (contactsModalIsOpen) return;

		this.setState({ activeSlide: number });
	}
	openContactsModal() {
		this.setState({ contactsModalIsOpen: true });
	}
	render() {
		const { hamburgerMenuIsOpen, isMobileView, activeSlide, contactsModalIsOpen } = this.state;
		const isMobileOrTablet = isMobileView || isMobile || isTablet;

		return (
			<div className="landing-page">
				{isMobileOrTablet && <LandingPageHeader
					color="light"
					changeSlide={this.changeSlide}
					isMobileView={isMobileOrTablet}
					toggleHamburgerMenu={this.toggleHamburgerMenu}
					openContactsModal={this.openContactsModal}
				/>}
				<OneScreenSlider
					activeSlide={activeSlide}
					changeSlide={this.changeSlide}
					isMobileView={isMobileOrTablet}
					hamburgerMenuIsOpen={hamburgerMenuIsOpen}
					toggleHamburgerMenu={this.toggleHamburgerMenu}
					openContactsModal={this.openContactsModal}
				>
					<TopSection />
					<AboutUsSection />
					<OurApproachSection />
					<OurApproachPortfolio />
					<OurApproachSliderSection />
					<OurApproachEstimateSection />
					<OurApproachListSection />
					<OurExperiencesSection />
					<HomeSection />
				</OneScreenSlider>
				{isMobileOrTablet && hamburgerMenuIsOpen && <HamburgerMenu
					isOpen={hamburgerMenuIsOpen}
					onClose={this.toggleHamburgerMenu}
					changeSlide={this.changeSlide}
					openContactsModal={this.openContactsModal}
				/>}
				{contactsModalIsOpen && <ContactsModal
					isOpen={contactsModalIsOpen}
					onClose={() => this.setState({ contactsModalIsOpen: false })}
				/>}
			</div>
		)
	}
}
