import React from 'react';
import classnames from 'classnames';
import { Tween } from 'react-gsap';
import { Element } from 'react-scroll'
import './OurApproachSection.scss';
import LandingPageHeader from './LandingPageHeader.jsx';
import SocialIcons from './SocialIcons';
import Container from '../../../components/Container';
import Orbits from '../../../components/Orbits';
import Button from '../../../components/Button';
import WaveBackground from '../../../components/WaveBackground';

export default function OurApproachSection({ isActive, style, changeSlide, isMobileView, toggleHamburgerMenu, openContactsModal }) {
   const playState = `${isActive ? 'play' : 'reverse'}`;
   return (
      <section className={classnames('our-appoach-section landing-page-gradient-bg one-screen-slide', { 'one-screen-slide--active': isActive })}  style={style}>
         {isMobileView && <Element name="ourApproachSection" />}
         {!isMobileView && <LandingPageHeader
            color="light"
            changeSlide={changeSlide}
            isMobileView={isMobileView}
            toggleHamburgerMenu={toggleHamburgerMenu}
            activeItem="Onze Aanpak"
            openContactsModal={openContactsModal}
         />}
         {!isMobileView && <SocialIcons color="light" isActive={isActive} />}
         <Orbits />
         <div className="landing-page-content-wrap">
            <Container>
               <Tween from={{ y: -200, opacity: 0 }} duration={1.5} playState={playState}>
                  <h2 className="landing-page--title">Onze Aanpak</h2>
               </Tween>
               <Tween from={{ y: -100, opacity: 0 }} duration={1.5} playState={playState}>
                  <p className="landing-page--description -white-color">Keenethics is een prominent ontwikkelingsbedrijf dat gespecialiseerd is in creatieve en innovatieve web ontwikkeling services dat gebruikt maakt van het potentieel dat de laatste technologische ontwikkelingen biedt.</p>
               </Tween>
               <Tween from={{ y: -100, opacity: 0 }} duration={1.5} playState={playState}>
                  <p className="landing-page--description -white-color">Als een web ontwikkelings bedrijf leveren wij hoge kwaliteit web ontwikkeling diensten voor onze wereldwijde klanten in verschillende industrieën. Ons agile web app team maakt gebruik van moderne methoden die gebruik maken van de expertise en data om er voor te zorgen dat er in de diverse behoefte van onze klanten wordt voorzien.</p>
               </Tween>
               <Button isActive={isActive} onClick={openContactsModal}>Neem contact met ons op</Button>
            </Container>
         </div>
         <WaveBackground color="white" />
      </section>
   )
}
