import React from 'react'
import classnames from 'classnames';
import { Element } from 'react-scroll'
import { Tween } from 'react-gsap';
import './OurExperiencesSection.scss';
import LandingPageHeader from './LandingPageHeader.jsx';
import SocialIcons from './SocialIcons';
import Container from '../../../components/Container';
import Orbits from '../../../components/Orbits';
import WaveBackground from '../../../components/WaveBackground';
import HorizontalSlider from '../../../components/HorizontalSlider';

export default function OurExperiencesSection({ isActive, style, changeSlide, isMobileView, toggleHamburgerMenu, openContactsModal }) {
   const quotesList = [
      {
         author: 'Wim Honkoop',
         jobTitle: 'My Grass Growth',
         authorImg: './images/landing-page/quotes/wim.jpeg',
         quote: 'Despite the physical distance and never meeting each other in person, Keenethics has built a complicated dairy industry related webapp for us. I found the cooperation in the development of this tool good, honest and straightforward.',
      },
      {
         author: 'Maarten Pieter Overvliet',
         jobTitle: 'The Check Network',
         authorImg: './images/landing-page/quotes/maarten.jpeg',
         quote: 'Due to their responsiveness & patience, the collaboration itself progressed smoothly from start to finish, resulting in a high-quality product & ensuring their continued engagement.',
      },
      {
         author: 'Andreas Feller',
         jobTitle: 'WorldLabs',
         authorImg: './images/landing-page/quotes/andreas-feller.jpg',
         quote: 'Keenethics provided necessary resources for developing the global platform. Their team’s approach was collaborative and proactive. Their software development expertise and dedication to the project are notable strengths of the company.',
      },
   ];

   const playState = `${isActive ? 'play' : 'reverse'}`;

   return (
      <section className={classnames('our-experiences-section landing-page-gradient-bg one-screen-slide', { 'one-screen-slide--active': isActive })} style={style}>
         {isMobileView && <Element name="ourExperiencesSection" />}
         {!isMobileView && <LandingPageHeader
            color="light"
            changeSlide={changeSlide}
            isMobileView={isMobileView}
            toggleHamburgerMenu={toggleHamburgerMenu}
            activeItem="Ervaringen"
            openContactsModal={openContactsModal}
         />}
         {!isMobileView && <SocialIcons color="light" isActive={isActive} />}
         <Orbits />
         <div className="landing-page-content-wrap">
            <Container>
               <Tween from={{ y: -200, opacity: 0 }} duration={1.5} playState={playState}>
                  <h2 className="landing-page--title -our-experiences">Ervaringen</h2>
                  <p className="our-experiences--description">Wat zeggen andere over ons?</p>
               </Tween>
                  <div className="our-experiences-slider">
                  <HorizontalSlider
                     slidesToShow={1}
                     buttonsType="numbers"
                     viewMoreLink="//clutch.co/profile/keenethics#reviews"
                     isActive={isActive}
                  >
                     {
                        quotesList.map(({ author, quote, authorImg, jobTitle }) => (
                           <li className="our-experiences-slider--item" key={author}>
                              {isMobileView && <p className="our-experiences-slider--quote">{quote}</p>}
                              <figure className="our-experiences-slider--figure">
                                 <div className="our-experiences-slider--image-wrap">
                                    <img src={authorImg} alt={author} />
                                 </div>
                                 <figcaption className="our-experiences-slider--figcaption">
                                    {!isMobileView && <p className="our-experiences-slider--quote">{quote}</p>}
                                    <h3 className="our-experiences-slider--author">{author}</h3>
                                    <p className="our-experiences-slider--job-title">{jobTitle}</p>
                                 </figcaption>
                              </figure>
                           </li>
                        ))
                     }
                  </HorizontalSlider>
               </div>
            </Container>
         </div>
         <WaveBackground color="white" />
      </section>
   )
}
