import React from 'react';
import classnames from 'classnames';
import { Tween } from 'react-gsap';
import './TopSection.scss';
import LandingPageHeader from './LandingPageHeader.jsx';
import SocialIcons from './SocialIcons';
import OurContacts from './OurContacts';
import Container from '../../../components/Container';
import Button from '../../../components/Button';
import Orbits from '../../../components/Orbits';
import WaveBackground from '../../../components/WaveBackground';

export default function TopSection({ isActive, style, changeSlide, isMobileView, toggleHamburgerMenu, openContactsModal }) {
   const socialIcons = [
      {
         title: 'Upwork',
         icon: <svg style={{ margin: '-4px' }} width="31px" height="31px" viewBox="0 0 31 31" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink"><defs><filter x="-28.6%" y="-28.6%" width="157.1%" height="157.1%" filterUnits="objectBoundingBox" id="filter-1"><feGaussianBlur stdDeviation={2} in="SourceGraphic"></feGaussianBlur></filter></defs><g id="Page-1" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd"><g id="Artboard" transform="translate(-1267.000000, -7366.000000)" fill="#5b659b"><g id="upwork_blue" transform="translate(1272.000000, 7371.000000)"><path d="M2,0 L19,0 C20.1045695,-2.02906125e-16 21,0.8954305 21,2 L21,19 C21,20.1045695 20.1045695,21 19,21 L2,21 C0.8954305,21 1.3527075e-16,20.1045695 0,19 L0,2 C-1.3527075e-16,0.8954305 0.8954305,2.02906125e-16 2,0 Z M14.6507812,12.1775391 C13.9535156,12.1775391 13.2972656,11.8822266 12.7066406,11.4023437 L12.8501953,10.7214844 L12.8542969,10.696875 C12.9855469,9.975 13.3916016,8.7609375 14.6507812,8.7609375 C15.5941406,8.7609375 16.3611328,9.52792969 16.3611328,10.4712891 C16.3611328,11.4146484 15.5941406,12.1775391 14.6507812,12.1775391 Z M14.6507812,7.03007812 C13.0429687,7.03007812 11.8001953,8.071875 11.2916016,9.79042969 C10.5205078,8.6296875 9.93398437,7.23925781 9.59355469,6.06621094 L7.86269531,6.06621094 L7.86269531,10.565625 C7.86269531,11.4556641 7.14082031,12.1775391 6.25078125,12.1775391 C5.36074219,12.1775391 4.63886719,11.4556641 4.63886719,10.565625 L4.63886719,6.06210937 L2.91210937,6.06210937 L2.91210937,10.5615234 C2.91210937,12.403125 4.40917969,13.9166016 6.25078125,13.9166016 C8.09238281,13.9166016 9.58945312,12.403125 9.58945312,10.5615234 L9.58945312,9.80683594 C9.92578125,10.5082031 10.3359375,11.2177734 10.8363281,11.8412109 L9.778125,16.8205078 L11.5458984,16.8205078 L12.3128906,13.2111328 C12.9855469,13.6417969 13.7566406,13.9125 14.6425781,13.9125 C16.5375,13.9125 18.0796875,12.3621094 18.0796875,10.4630859 C18.0837891,8.57226562 16.5457031,7.03007812 14.6507812,7.03007812 Z" id="Combined-Shape" opacity="0.8"></path></g></g></g></svg>,
         href: '//upwork.com/o/companies/~0106b5437592391f94',
      },
      {
         title: 'Clutch',
         icon: <svg width="21px" id="clutch" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 70.2 79.3"><defs></defs><path className="cls-1" d="M55.1,57A22.35,22.35,0,0,1,40,62.6c-12.8,0-22.2-9.4-22.2-22.3S26.9,18.4,40,18.4a22.9,22.9,0,0,1,15.2,5.5L58,26.3,70.4,13.9,67.3,11A40.13,40.13,0,0,0,40,.8C17,0.8.3,17.4,0.3,40.2S17.4,80.1,40,80.1A40.14,40.14,0,0,0,67.5,69.7l3-2.8L57.9,54.5Z" transform="translate(-0.3 -0.8)"/><circle className="cls-2" cx="39.1" cy="39.6" r="13.3"/></svg>,
         href: '//clutch.co/profile/keenethics',
      },
   ];

   const playState = `${isActive ? 'play' : 'reverse'}`;

   return (
      <section className={classnames('top-section landing-page-gradient-bg one-screen-slide', { 'one-screen-slide--active': isActive })} style={style}>
         {!isMobileView && <LandingPageHeader
            color="light"
            changeSlide={changeSlide}
            isMobileView={isMobileView}
            toggleHamburgerMenu={toggleHamburgerMenu}
            openContactsModal={openContactsModal}
         />}
         {!isMobileView && <SocialIcons color="light" isActive={isActive} isTopSection />}
         {!isMobileView && <OurContacts color="light" isActive={isActive} />}
         <Orbits />
         <div className="landing-page-content-wrap">
            <Container>
               <Tween from={{ y: -200, opacity: 0 }} duration={1.5} playState={playState}>
                  <h1 className="top-section--title">Ethische ontwikkeling van webapplicaties</h1>
               </Tween>
               <Tween from={{ y: -100, opacity: 0 }} duration={1.5} playState={playState}>
                  <p className="top-section--description">Jij hebt een slim idee <span className="dash" /> Wij hebben een slimme aanpak.</p>
                  <p className="landing-page--description -white-color">Wij bedenken en ontwikkelen responsive web- en cross-platform applicaties met als doel onze  klanten te helpen hun doelen te bereiken.</p>
                  <p className="landing-page--description -white-color">Keenethics geeft bedrijven krachtige tools om end-to-end (web) applicaties ontwerpen. Dit alles terwijl wij ook moderne web design services en oplossingen bieden om gebruikers te koppelen en te verbinden.</p>
               </Tween>
               <a
                  href="//keenethics.com"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="top-section--link"
               >
                  <Button isActive={isActive}>Ontdek meer</Button>
               </a>
            </Container>
         </div>
         {
            !isMobileView && <div className="top-section-social-icons">
               <Tween from={{ x: -100, opacity: 0, delay: 2 }} duration={1} playState={playState}>
                  <ul className="top-section-social-icons--list">
                     {
                        socialIcons.map(({ title, href, icon}) => (
                           <li className="top-section-social-icons--item" key={title}>
                              <a
                                 href={href}
                                 target="_blank"
                                 rel="noopener noreferrer"
                                 className="top-section-social-icons--link"
                              >{icon}<span className="top-section-social-icons--link-title">{title}</span></a>
                           </li>
                        ))
                     }
                  </ul>
               </Tween>
            </div>
         }
         <WaveBackground color="white" />
      </section>
   )
}
