import React from 'react'
import PropTypes from 'prop-types';
import './Orbits.scss';

export default function Orbits({ colorRgb, position, scale }) {
   const orbits = [1, 2, 3, 4];
   return (
      <div className="orbits-wrap" style={{ top: position.positionX, left: position.positionY }}>
         {
            orbits.map(orbit => {
               const orbitStyles = {
                  width: `${15 * orbit * scale + 1}vw`,
                  height: `${15 * orbit * scale + 1}vw`,
                  borderColor: `rgba(${colorRgb}, ${1 / (orbit + Math.cos(orbit)) - .15})`,
               }
               return (
                  <div
                     key={orbit}
                     className={`orbit`}
                     style={orbitStyles}
                  />
               )
            })
         }
      </div>
   )
}

Orbits.propTypes = {
   position: PropTypes.object,
   colorRgb: PropTypes.string,
   scale: PropTypes.number,
};
Orbits.defaultProps = {
   position: {
      positionX: '50%',
      positionY: '50%',
   },
   colorRgb: '255, 255, 255',
   scale: 1.5,
}