import React from 'react';
import classnames from 'classnames';
import { Tween } from 'react-gsap';
import { Element } from 'react-scroll'
import './AboutUsSection.scss';
import LandingPageHeader from './LandingPageHeader.jsx';
import SocialIcons from './SocialIcons';
import Container from '../../../components/Container';
import Orbits from '../../../components/Orbits';
import WaveBackground from '../../../components/WaveBackground';
import AnimatedCounter from '../../../components/AnimatedCounter';

export default function AboutUsSection({ isActive, style, changeSlide, isMobileView, toggleHamburgerMenu, openContactsModal }) {
   const counter = [
      {
         number: [3, 0],
         description: 'software engineers',
      },
      {
         number: [6, 0],
         description: 'happy customers',
      },
      {
         number: [1, 0, 0, 0],
         description: 'hrs spent on customer Skype calls',
      },
      {
         number: [1, 0],
         description: 'business domains',
      },
   ];
   const playState = `${isActive ? 'play' : 'reverse'}`;
   return (
      <section
         className={classnames('about-us-section one-screen-slide', { 'one-screen-slide--active': isActive })}
         style={style}
      >
         {isMobileView && <Element name="aboutUsSection" />}
         {!isMobileView && <LandingPageHeader
            color="dark"
            changeSlide={changeSlide}
            isMobileView={isMobileView}
            toggleHamburgerMenu={toggleHamburgerMenu}
            activeItem="Wie zijn wij"
            openContactsModal={openContactsModal}
         />}
         {!isMobileView && <SocialIcons color="dark" isActive={isActive} />}
         <Orbits colorRgb="222, 79, 79" />
         <div className="landing-page-content-wrap">
            <Container>
               <Tween from={{ y: -200, opacity: 0 }} duration={1.5} playState={playState}>
                  <h2 className="landing-page--title">Wie zijn wij</h2>
               </Tween>
               <Tween from={{ y: -100, opacity: 0 }} duration={1.5} playState={playState}>
                  <p className="landing-page--description">Keenethics is een betrouwbare leverancier van custom web development services. We hebben onze naam gevestigd door onze uitstekende open communicatie en onze kwaliteit van werk.</p>
                  <p className="landing-page--description">Ons team zorgt voor schaalbare en realistische oplossingen die van A tot Z aan al uw ontwikkeling benodigdheden voldoen.</p>
               </Tween>
               <div className="about-us-section-counter">
                  <Tween from={{ y: 100, opacity: 0 }} duration={2} playState={playState}>
                     <ul className="about-us-section-counter--list">
                        {
                           counter.map(({ description, number }) => (
                              <li className="about-us-section-counter--item" key={description}>
                                 <AnimatedCounter
                                    description={description}
                                    number={number}
                                    additionalСharacter="+"
                                    counterIsVisible={isActive}
                                 />
                              </li>
                           ))
                        }
                     </ul>
                  </Tween>
               </div>
            </Container>
         </div>
         <WaveBackground color="red" />
      </section>
   )
}